/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable @next/next/no-img-element */
import { useState, useEffect } from "react";
import { useRouter } from "next/router";
import { CloseCircleOutlined, SaveOutlined } from "@ant-design/icons";
import { Button, Card, Col, Row, Tabs } from "antd";
import { CAddAssetInputBatch, CTitle, CPageTitle } from "@components";
import { FSubmitBack } from "@function";
import CApi from "@api";

const { TabPane } = Tabs;
const NewAsset = () => {
  useEffect(() => {
    getListMaster();
  }, []);
  const router = useRouter();
  const [departement, setDepartement] = useState({ dataDepartement: [] });
  const [listLocation, setListLocation] = useState({ dataLocation: [] });
  const [listBranch, setListBranch] = useState({ dataBranch: [] });
  const [listStatus, setListStatus] = useState({ dataStatus: [] });
  const [listAssetType, setListAssetType] = useState({ dataAssetType: [] });
  const [listCodeActiva, setListCodeActiva] = useState({ dataCodeActiva: [] });
  const [listOwnership, setListOwnership] = useState({ dataOwnership: [] });
  const [listAnggaran, setListAnggaran] = useState({ dataAnggaran: [] });
  const [error, setError] = useState("");
  const [isSubmit, setIsSubmit] = useState(false);

  const [forms, setForms] = useState({
    id: "",
    no_induk_barang: "",
    tag_number: "",
    name: "",
    asset_category_id: "",
    trademark: "",
    merek_barang: "",
    model: "",
    color: "",
    serial_number: "",
    part_number: "",
    anggaran_id: "",
    qty: "",
    status_activa: "",
    value: "",
    masa_manfaat: "",
    year: "",
    location_id: "",
    unit_id: "",
    uselife: "",
    type: "",
    vendor_id: "",
    status_id: "",
    branch_id: "",
    remark: "",
    picture: "",
    no_barang: "",
    flag: "",
    asset_type_id: "",
    code_activa_id: "",
    ownership_id: "",
    file_name_satu: "",
    file_description_satu: "",
    file_name_dua: "",
    file_description_dua: "",
    file_name_tiga: "",
    file_description_tiga: "",
    tgl_perolehan: "",
    payment_voucher: "",
    harga_perolehan: "",
    currency_perolehan: "",
  });

  // getData List Option
  const getListMaster = async () => {
    await CApi.get("api/list-master").then((res) => {
      setDepartement({
        ...departement,
        dataDepartement: res.data.vendorList,
      });
      setListStatus({
        ...listStatus,
        dataStatus: res.data.statusList,
      });
      setListLocation({
        ...listLocation,
        dataLocation: res.data.location,
      });
      setListBranch({
        ...listBranch,
        dataBranch: res.data.branchList,
      });
      setListAssetType({
        ...listAssetType,
        dataAssetType: res.data.assetType,
      });
      setListCodeActiva({
        ...listCodeActiva,
        dataCodeActiva: res.data.codeActiva,
      });
      setListOwnership({
        ...listOwnership,
        dataOwnership: res.data.ownership,
      });
      setListAnggaran({
        ...listAnggaran,
        dataAnggaran: res.data.anggaran,
      });
    });
  };
  // Last getData List Option

  const handleChange = (evt) => {
    const newData = { ...forms };
    newData[evt.target.id] = evt.target.value;
    setForms(newData);
  };

  const onChangeDate = (value, dateString) => {
    setForms({
      ...forms,
      uselife: dateString,
    });
  };
  const onChangeDatePerolehan = (value, dateString) => {
    setForms({
      ...forms,
      tgl_perolehan: dateString,
    });
  };

  const onRemark = ({ target: { value } }) => {
    setForms({
      ...forms,
      remark: value,
    });
  };

  // Fungsi Select Option
  function onBranch(value) {
    setForms({
      ...forms,
      branch_id: value,
    });
  }
  function onLocation(value) {
    setForms({
      ...forms,
      location_id: value,
    });
  }
  function onDepartement(value) {
    setForms({
      ...forms,
      vendor_id: value,
    });
  }
  function onStatus(value) {
    setForms({
      ...forms,
      status_id: value,
    });
  }
  function onAssetType(value) {
    setForms({
      ...forms,
      asset_type_id: value,
    });
  }
  function onCodeActiva(value) {
    setForms({
      ...forms,
      code_activa_id: value,
    });
  }
  function onOwnersip(value) {
    setForms({
      ...forms,
      ownership_id: value,
    });
  }
  function onCurrency(value) {
    setForms({
      ...forms,
      currency_perolehan: value,
    });
  }

  function onActiva(value) {
    setForms({
      ...forms,
      status_activa: value,
    });
  }
  function onFlag(value) {
    setForms({
      ...forms,
      flag: value,
    });
  }

  function onAnggaran(value) {
    setForms({
      ...forms,
      anggaran_id: value,
    });
  }
  // Akhir Fungsi Select Option

  const handleSubmit = async () => {
    await FSubmitBack(
      "api/asset/parent-creation-assets",
      forms,
      setError,
      setIsSubmit,
      router
    );
  };

  return (
    <Row>
      <CTitle />
      <Col span={24}>
        <Card
          cover={
            <Col
              span={24}
              style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
            />
          }
        >
          <CPageTitle />
          <Tabs defaultActiveKey="1">
            <TabPane tab="Data" key="1">
              <CAddAssetInputBatch
                forms={forms}
                handleChange={handleChange}
                listAssetType={listAssetType}
                onAssetType={onAssetType}
                error={error}
                onBranch={onBranch}
                listBranch={listBranch}
                onChangeDate={onChangeDate}
                onLocation={onLocation}
                listLocation={listLocation}
                onDepartement={onDepartement}
                departement={departement}
                onStatus={onStatus}
                listStatus={listStatus}
                onRemark={onRemark}
                listCodeActiva={listCodeActiva}
                onCodeActiva={onCodeActiva}
                listOwnership={listOwnership}
                onOwnersip={onOwnersip}
                onCurrency={onCurrency}
                onActiva={onActiva}
                onFlag={onFlag}
                listAnggaran={listAnggaran}
                onAnggaran={onAnggaran}
                onChangeDatePerolehan={onChangeDatePerolehan}
              />
            </TabPane>
          </Tabs>
          <Row gutter={5} style={{ float: "right" }}>
            <Col>
              <Button
                icon={<CloseCircleOutlined />}
                style={{
                  backgroundColor: "#F0AD4E",
                  color: "#fff",
                  marginRight: 15,
                }}
                onClick={() => router.push("/asset")}
                disabled={isSubmit}
              >
                Cancel
              </Button>
              <Button
                icon={<SaveOutlined />}
                type="primary"
                onClick={() => handleSubmit()}
                disabled={isSubmit}
              >
                Save
              </Button>
            </Col>
          </Row>
        </Card>
      </Col>
    </Row>
  );
};

export default NewAsset;
